export default class Video {
    constructor(title, preview, author, date, review, likes, dislikes, id) {
        this.title = title;
        this.preview = preview;
        this.author = author;
        this.date = date;
        this.review = review;
        this.link = 'https://www.youtube.com/watch?v=' + id;
        this.likes = likes;
        this.dislikes = dislikes;

        this.composeElement();
    }

    composeElement() {
        this.videoItem = document.createElement('li');
        this.videoItem.classList.add('video-list-item');
        this.videoItem.innerHTML = `
            <div class="title" style="background: url(${this.preview}) 50% 50% no-repeat; background-size: cover">
                <a href="${this.link}" target="_blank"><h2 class="video-header">${this.title}</h2></a>
            </div>
            <div class="description">
                <ul>
                    <li class="author">${this.author}</li>
                    <li class="date">${new Date(this.date).toISOString().slice(0, 10)}</li>
                    <li class="review">${this.review}</li>
                    <li class="like">${this.likes}</li>
                    <li class="dislike">${this.dislikes}</li>
                </ul>    
            </div>
        `;
    }

    get video() {
        return this.videoItem;
    }
}


