let nextPageToken;
let queryString;

export default function loadVideos(query) {
    queryString = query;

    return getVideos(queryString)
        .then(getStatistics);
}

function getVideos(query) {
    return fetch(`https://www.googleapis.com/youtube/v3/search?key=AIzaSyDrURTYYuvbS21TDqNHvi_8SHT8tMZYqXA&type=video&part=snippet&maxResults=20&q=${query}`)
        .then(res => res.json());

}

function getStatistics(videos) {
    const idArray = [];

    videos.items.forEach(item => {
        idArray.push(item.id.videoId);
    });
    nextPageToken = videos.nextPageToken;

    return fetch(`https://www.googleapis.com/youtube/v3/videos?key=AIzaSyDrURTYYuvbS21TDqNHvi_8SHT8tMZYqXA&id=${idArray.join(',')}&part=snippet,statistics`)
        .then(res => res.json())
        .then(res => res.items);
}

export function getNextVideos() {
    return fetch(`https://www.googleapis.com/youtube/v3/search?key=AIzaSyDrURTYYuvbS21TDqNHvi_8SHT8tMZYqXA&pageToken=${nextPageToken}&part=snippet&maxResults=20&q=${queryString}`)
        .then(res => res.json())
        .then(getStatistics);
}


